form {
  background-color: #1e1e1e;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.auth_page {
  overflow-y: hidden;
  height: 100%;
}

input {
  border-width: 0 0 1px;
  border-color: #2f808b;
  background-color: #1e1e1e;
  padding: 12px 15px;
  margin: 8px 0;
  color: gainsboro;
  width: 70%;
}

.container {
  background-color: #252526;
  display: block;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 850px;
  max-width: 100%;
  min-height: 650px;
  margin: 125px auto;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.5s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 80%;
  z-index: 2;
}

.container.right-panel-active .sign-in-container {
  transform: translateX(120%);
  animation: show 0.6s;
}

.sign-up-container {
  left: 0;
  width: 80%;
  opacity: 0;
  z-index: 1;
}

.container.right-panel-active .sign-up-container {
  transform: translateX(25%);
  opacity: 1;
  z-index: 3;
  animation: show 0.6s;
}

@keyframes show {
  0%,
  100% {
    opacity: 0;
    z-index: 1;
  }
  0%,
  100% {
    opacity: 1;
    z-index: 3;
  }
}

.overlay-container {
  position: absolute;
  top: 0;
  width: 20%;
  right: 0px;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 3;
}

.overlay-container:hover {
  cursor: pointer;
}

.container.right-panel-active .overlay-container {
  transform: translateX(-100%);
  left: 20%;
}

.overlay {
  background: #1e1e1e;
  background: -webkit-linear-gradient(to right, #2f808b, rgb(37, 101, 109));
  background: linear-gradient(to right, #2f808b, rgb(37, 101, 109));
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: absolute;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
  display: flex;
}

.container.right-panel-active .overlay {
  transform: translateX(50%);
}
@media (max-width: 800px) {
  .overlay-panel {
    display: none !important;
  }
}
.overlay-panel {
  position: absolute;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0px 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 80px;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.container.right-panel-active .overlay-left {
  transform: translateX(0);
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.container.right-panel-active .overlay-right {
  transform: translateX(20%);
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.icon {
  width: 100%;
  border-radius: 50%;
}

a.forgotLink {
  color: #ffffff;
  font-size: 14px;
  font-family: "Orbitron", sans-serif;
  text-decoration: none;
  margin: 15px 0;
}

a.forgotLink:hover {
  color: #2f808b;
}
